import React, { Component } from 'react';
import Buscador from './componentes/Buscador';
import Resultado from './componentes/Resultado';

class App extends Component {
  state={
    palabra:'',
    pagina:'',
    fotos:[]
  }

  scroll=()=>{
    const elemento=document.querySelector('.jumbotron');
    elemento.scrollIntoView('smooth','end');
  }
  paginaAnterior=()=>{
    //console.log("regreso");
    let pagina = this.state.pagina;
    if(pagina===1) return null;
    pagina--;
    this.setState({
      pagina
    },()=>{
      this.listaFotos();
      this.scroll();
    });
    console.log(pagina);
  }
  paginaSiguiente=()=>{
    //console.log("sigo");
    let pagina = this.state.pagina;
    pagina++;
    this.setState({
      pagina
    },()=>{
      this.listaFotos();
      this.scroll();
    });
    console.log(pagina);
  }
  listaFotos = () =>{
    const palabra=this.state.palabra;
    const pagina=this.state.pagina
    const sitio=`https://pixabay.com/api/?key=24744126-9cebcfeaf70b56b93efab1ff1&q=${palabra}&per_page=30&page=${pagina}`;
     console.log(sitio);
     fetch(sitio)
     .then(respuesta => respuesta.json())
     .then(resultado => this.setState({fotos:resultado.hits}))
  }
  datosBusqueda = (palabra) => {
    //console.log(palabra);
    this.setState({
      palabra: palabra,
      pagina: 1
    }, ()=>{
      this.listaFotos();
    })
  }
  render() { 
    return(
      <div className="app container">
        <div className="jumbotron">
          <p className="lead text-center">Buscar Foto</p>
          <Buscador 
          datosBusqueda={this.datosBusqueda}
          />
        </div>
        <div className="row justify-content-center">
          <Resultado
          imagenes={this.state.fotos}
          paginaAnterior={this.paginaAnterior}
          paginaSiguiente={this.paginaSiguiente}
          />
        </div>
        {this.state.palabra}
      </div>
    );
  }
}
 
export default App;